/* Change scroll behavior for all html to smooth */
html {
  scroll-behavior: smooth;
}

/* Change h1 font-family */
h1 {
  font-family: "Work Sans", sans-serif;
}

/* Adjust left padding on navbar */
.navbar {
  padding-left: 0rem;
}

/* Remove border from button in navbar */
.navbar button {
  border: 0px;
}

/* Remove border from button in navbar */
.navbar button:focus {
  border: 0px;
  outline: 0px;
}

/* Text align to center class */
.align-center {
  text-align: center;
}

/* Font Awesome down arrow styling */
#down-arrow {
  display: block;
  font-size: 50px;
  margin-top: 100px;
}

/* Remove text decoration on hover of down arrow */
.jumbotron a:hover {
  text-decoration: none;
}

/* Hero image styling */
#rocky-image {
  width: 350px;
  margin-top: 50px;
  border-radius: 200px;
}

/* Custom blue gradient used on nav bar and index.html about me section */
.my-gradient {
  background: #00c6ff; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to top,
    #0072ff,
    #00c6ff
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to top,
    #0072ff,
    #00c6ff
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/* My gradient hover color for buttons */
a.my-gradient:hover {
  background: #0072ff; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to top,
    #0072ff,
    #00a5d3
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to top,
    #0072ff,
    #00a5d3
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

/* My gradient for font color */
.font-my-gradient {
  color: #0072ff; /* fallback for old browsers */
}

/* My gradient hover color for font */
.font-my-gradient:hover {
  color: #0056b3; /* fallback for old browsers */
}

/* hr for portfolio width */
.my-hr {
  width: 10%;
}

/* Spacing for the card portfolio section */
.card-top-spacing {
  margin-top: 50px;
}

/* Increase top margin of h2 headers */
#portfolio h2,
#about-me h2 {
  margin-top: 20px;
}

/* Adjust font awesome social media icon sizes and set transition */
#social {
  font-size: 30px;
  min-height: 50px;
  transition: 0.25s;
}

/* Change size on hover */
#social a:hover {
  font-size: 32px;
  transition: 0.25s;
}

/* Center footer text and adjust margin */
footer {
  text-align: center;
  margin: 50px auto;
}

/* Adjust font awesome cloud icon in footer */
#footer-icon {
  font-size: 50px;
  margin-top: 20px;
}

/* Media query to change scroll behavior for prefered-reduced motion */
@media screen and (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
}

/* Media query for screen sizes larger than mobile */
@media only screen and (min-width: 640.08px) {
  /* hide on tablet and desktop */
  .hide-tablet-and-desktop {
    display: none;
  }
}

/* Media query for smaller screen sizes */
@media only screen and (max-width: 640px) {
  /* Adjust h1 font-size on smaller screen */
  h1 {
    font-size: 40px !important;
  }

  /* Adjust nav font-size on mobile */
  .nav {
    font-size: 13px;
  }

  /* Adjust nav bar spacing and center text */
  #nav-bar {
    margin: 0px;
    padding: 0px;
    text-align: center;
    justify-content: center;
  }

  /* Make 'Rocky Salerno' nav item fill the full width */
  #name-banner {
    margin: 0px;
    width: 100%;
  }

  /* Remove Contact Nav Item Button border */
  #outline-button {
    border: 0px solid white;
    font-size: 13px;
  }

  /* Change Contact Nav Item Button hover effects */
  .btn-outline-primary:hover {
    color: #0056b3;
    background-color: #ffffff;
    border-color: #ffffff;
  }

  /* Align text left on mobile */
  .mobile-text-left-align {
    text-align: left;
  }

  /* Change contact form container width to align center */
  #contact-section {
    margin: 0px auto !important;
  }
}
